import React from 'react'
import { useLoaderData, useNavigate } from 'react-router-dom';

// project
import CustomizedAvatarWithUser from 'components/components/CustomizedAvatarWithUserAndDate'
import { Box, Card, CardContent, CardMedia, Divider, IconButton, Stack, Typography } from '@mui/material';
import CustomizedImage from 'components/components/CustomizedImage';
import CustomizedComment from 'components/components/CustomizedComment';
import CustomizedCommentSend from 'components/components/CustomizedComment/CommenSend';
import { Parser } from 'html-to-react';
import PageContainer from 'layouts/PageContainer';
import PageHeading from 'layouts/PageHeading';

// mocks
import comments from '__mocks__/comments'
import { CONTENT as content } from '__mocks__/html-content'
import BlogSmallCard from '../../components/blog/BlogSmallCard';

import { POSTS } from '__mocks__/blogs';

export default function BlogDetail(props) {
  const post = useLoaderData();

  return (
    <PageContainer>
      <PageHeading title="Blog Detail" back />
      <Box sx={{
        display: 'grid',
        gridAutoFlow: 'row'
      }}>
        <Box sx={{ gridColumn: '1/6', gridRow: '1/2', p: 3 }}>
          <Stack spacing={3}>
            <Typography variant='h1' gutterBottom>
              {post.title}
            </Typography>

            <Typography variant='4' gutterBottom>
              {post.paragraph}
            </Typography>

            <CustomizedAvatarWithUser name={post.author.name} createdAt={post.createdAt} avatar={post.author.avatarUrl} sx={{ my: 2 }} />

            <CustomizedImage
              url={post.cover}
              sx={{
                width: 800,
                height: 540,
                margin: 'auto',
                borderRadius: 2
              }}
            />
            <Box component='div' sx={{ p: 2, fontSize: 18 }}>
              {Parser().parse(content)}
            </Box>
            <Divider />

            <CustomizedCommentSend author={{ avatarUrl: post.author.avatarUrl }} />

            {comments.map((comment, index) => (
              <CustomizedComment {...comment} />
            ))}
          </Stack>
        </Box>
        <Box sx={{
          gridColumn: '7/9',
          gridRow: '1/2',
          mt: 5
        }}>
          <Box
            sx={{
              p: 2,
              bgcolor: 'background.default',
              display: 'flex',
              flexDirection: 'column',
              gap: 2,
              border: 1,
              borderRadius: 2,
              borderColor: 'grey.300',
            }}
          >
            <Typography variant='h5' textAlign={'left'}>Upcoming updates</Typography>
            {POSTS.slice(1, 5).map((post) => (
              <BlogSmallCard post={post} />
            ))}
          </Box>
        </Box>
      </Box>
    </PageContainer>
  )
}
