import React from 'react'

// @mui
import { ArrowBack } from '@mui/icons-material'
import { Breadcrumbs, Button, Link, Stack, Typography } from '@mui/material'

export default function PageHeading(props) {

    const { title, breadcrumbs, back, stats, action } = props
    return (
        <Stack
            direction="row"
            alignItems="center"
            justifyContent="space-between"
            sx={{
                mb: 2,
                // bgcolor: theme.palette.mode === 'dark' ? 'grey.700' : 'grey.300',
                // borderRadius: `${theme.shape.borderRadius}px`
            }}>
            <Stack justifyContent='flex-start' alignItems='flex-start'>
                {back &&
                    <Button
                        startIcon={<ArrowBack />}
                        onClick={() => window.history.back()}
                    >
                        Back
                    </Button>}

                {breadcrumbs && <Breadcrumbs aria-label="breadcrumb">
                    {breadcrumbs.map((breadcrumb, index) => (<Link key={index} underline="hover" color="inherit" pathname={breadcrumb.pathname}>
                        {breadcrumb.name}
                    </Link>))}
                </Breadcrumbs>}

                <Typography variant="h3" gutterBottom>
                    {title}
                </Typography>
                {stats}
            </Stack>
            {action}
        </Stack >
    )
}
